.shares{display:flex}.shares>div{height:100%;border-radius:1vh;display:grid;place-items:center;transition:.4s .1s cubic-bezier(0.2, 0, 0.4, 1);transition-property:width,filter}.shares>div:hover{width:12.5% !important;filter:saturate(1.2)}.shares>div::after{color:#fff;text-shadow:#000 0 0 .2em;font-size:1.25em;white-space:nowrap;opacity:0;transition:opacity .2s .2s ease-out}.shares>div:nth-child(1){background-color:#76c043}.shares>div:nth-child(1)::after{content:"3-9" " år"}.shares>div:nth-child(1):hover::after{opacity:1}.shares>div:nth-child(2){background-color:#55c09a}.shares>div:nth-child(2)::after{content:"10-15" " år"}.shares>div:nth-child(2):hover::after{opacity:1}.shares>div:nth-child(3){background-color:#0da8e1}.shares>div:nth-child(3)::after{content:"16-29" " år"}.shares>div:nth-child(3):hover::after{opacity:1}.shares>div:nth-child(4){background-color:#804e9f}.shares>div:nth-child(4)::after{content:"30-44" " år"}.shares>div:nth-child(4):hover::after{opacity:1}.shares>div:nth-child(5){background-color:#da4597}.shares>div:nth-child(5)::after{content:"45-59" " år"}.shares>div:nth-child(5):hover::after{opacity:1}.shares>div:nth-child(6){background-color:#ee3e23}.shares>div:nth-child(6)::after{content:"60-74" " år"}.shares>div:nth-child(6):hover::after{opacity:1}.shares>div:nth-child(7){background-color:#f6921e}.shares>div:nth-child(7)::after{content:"75+" " år"}.shares>div:nth-child(7):hover::after{opacity:1}.shares>div:nth-child(8){background-color:#000}.shares>div:nth-child(8)::after{content:"Telefon"}.shares>div:nth-child(8):hover::after{opacity:1}.shares:has(:hover)>div:not(:hover){filter:saturate(0.8) brightness(0.8)}.hints{position:absolute;display:flex;top:1vh;right:1vh}.hints>div{height:4vh;width:4vh;border-radius:1vh;display:grid;place-content:center;font-weight:bold;font-size:.75em}.hints>div.green{background-color:#0e2}.hints>div.yellow{background-color:#ff0}.hints>div.gray{background-color:#aaa}